import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navbar from './components/Navbar';
import { BrowserRouter as Router, Swtich, Route } from 'react-router-dom'

function App() {
    // Конструктор Главного компонента
    const [people, setPeople] = useState([])

    useEffect(() => {
        axios({
            method: 'GET',
            url: 'http://127.0.0.1:8000/api/test-api/'
        }).then((response) => {
            setPeople(response.data)
        })
    }, [])

    return (
        <div className="App">
            <Router>
                <Navbar />
                <header>
                    <h1>IT-BLOG PORTAL</h1>
                </header>
                <hr/>
                <main>
                    <h4>Динамический контент...</h4>
                </main>
                <hr/>
                <footer>
                    <h6> Copyright: &copy; ALBET LLC - Kyiv, 2021</h6>
                </footer>
            </Router>
        </div>
    );
}

export default App;
